import None from "@/components/qy/none.vue"
export default {
	data() {
		return {
			btn_name: 'cou_t_rr',
			c_index: 0,
			list: '',
			class_name: 'cou_t_l_01',
			state: 0,
			list_empty: false,
		};
	},
	components: {
		None,
	},
	onShow() {
		this._load()
	},
	methods: {
		jump_to(is_all) {
			let type = ''
			if (is_all == 0) {
				type = 'all'
			} else {
				type = 'coupon'
			}
			uni.navigateTo({
				url: '../../extend-view/productList/productList?cid=5&sid=0&type=' + type
			})
		},
		_load() {

			this.$api.http.get('coupon/user/get_coupon').then(res => {

				if (res.data=='') {
					this.list_empty = true
				} else {
					this.list = res.data
				}
				uni.stopPullDownRefresh();
			})
		},
		num(index) {
			if (index == 0) {
				this.c_index = index
				this.class_name = "cou_t_l_01"
				this.btn_name = "cou_t_rr"
				this.state = 0
			}
			if (index == 1) {
				this.c_index = index
				this.class_name = "cou_sss"
				this.btn_name = "btn_grey"
				this.state = 1
			}
			if (index == 3) {
				this.c_index = index
				this.class_name = "cou_sss"
				this.btn_name = "btn_grey"
				this.state = 3
			}
		},

	},

	onPullDownRefresh() {
		console.log('refresh');
		this._load();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
